import React, { Component } from 'react';
import Clock from 'react-live-clock';
import { Clock as Cl } from 'react-clock';


class HomePage extends Component {
  state = {
    date: new Date(),
  }

  componentDidMount() {
    setInterval(
      () => this.setState({ date: new Date() }),
      1000
    );
  }

  render() {
    const background = require('../static/background.jpg');
    const video = require('../static/recap.mp4');

    return (
      <div>
        <div className="home-text">
          Welcome!
        </div>
        <br/>
        <br/>
        <div className="home-sub-text">
          Here you can find information regarding my curriculum vitae, projects, <br/>
          lecture notes/slides and social static accounts. <br/> <br/>
          Feel free to check them out by clicking on the icons.
        </div>
        <div>
          <video id="intro-video" className="background-video" controls controlsList="nodownload">
            <source src={video} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
        <div className="dig-clock">
          <Clock format={'h:mm A'} ticking={true} />
        </div>
        <div className="date-clock">
          <Clock format={'dddd, MMMM Mo, YYYY'} ticking={true} />
        </div>
        <div className="analog-clock">
          <Cl value={this.state.date} renderNumbers="true"/>
        </div>
        <img src={background} alt="background" className="background-img no-padding" /> 
      </div>
    )
  }
}

export default HomePage;